import './App.css';
import { useState, useEffect } from 'react';

import { BrowserRouter as Router, Switch, Route, useParams} from 'react-router-dom';
import axios from 'axios';

import Index from './Views/Index';
import Header from './Components/Layout/Header';
import GlobalData from './Components/Index/GlobalData';

function App() {
  const [isLoaded, setIsLoaded] = useState(false);

  const [chart, setChart] = useState([]);
  const [news, setNews] = useState([]);
  const [trending, setTrending] = useState([]);
  const [fearAndGreed, setFearAndGreed] = useState([]);
  const [globalData, setGlobalData] = useState([]);
  const [global2Data, setGlobal2Data] = useState([]);
  const getAPIsData = () => {
    let endpoints = [
      'https://crypto-news-apiii.herokuapp.com/news/cointelegraph',
      'https://api.coingecko.com/api/v3/coins/markets?vs_currency=usd&order=market_cap_desc&per_page=25&page=1&sparkline=false',
      'https://api.coingecko.com/api/v3/search/trending',
      'https://api.alternative.me/fng/?limit=14',
      'https://api.coingecko.com/api/v3/global'
    ];
    let endpointsObj = {
        'news' : {
          url : 'https://crypto-news-apiii.herokuapp.com/news/cointelegraph',
          headers : {}
        },
        'market' : {
          url : 'https://api.coingecko.com/api/v3/coins/markets?vs_currency=usd&order=market_cap_desc&per_page=25&page=1&sparkline=false',
          headers : {}
        },
        'trending' : {
          url : 'https://api.coingecko.com/api/v3/search/trending',
          headers : {}
        },
        'fearandgreed' : {
          url :  'https://api.alternative.me/fng/?limit=14',
          headers : {}
        },
        'global' : {
          url :  'https://api.coingecko.com/api/v3/global',
          headers : {}
        },
        'global2' : {
          url :  'https://pro-api.coinmarketcap.com/v1/global-metrics/quotes/latest',
          headers: {
            'X-CMC_PRO_API_KEY': 'b54bcf4d-1bca-4e8e-9a24-22ff2c3d462c',
            
          }
        }
    } 

    Promise.all(Object.keys(endpointsObj).map(
      (key) => axios.get(endpointsObj[key].url), {
        headers: { }
      }))
      .then(
        ([ {data: news}, {data: chart}, {data: trending}, {data: fearandgreed}, {data: globalData}, {data: global2Data}])=> {
            console.log('done');
            setChart(chart)
            setNews(news)
            setTrending(trending)
            setFearAndGreed(fearandgreed)
            setGlobalData(globalData.data)
            setGlobal2Data(global2Data.data)
          }
      )
      .finally(() => {
        console.log('set loader');
        setIsLoaded(true);
      });

    // Promise.all(endpoints.map(
    //   (endpoint) => axios.get(endpoint)))
    //   .then(
    //     ([ {data: news}, {data: chart}, {data: trending}, {data: fearandgreed}, {data: globalData}])=> {
    //         console.log('done');
    //         setChart(chart)
    //         setNews(news)
    //         setTrending(trending)
    //         setFearAndGreed(fearandgreed)
    //         setGlobalData(globalData.data)
    //       }
    //   )
    //   .finally(() => {
    //     console.log('set loader');
    //     setIsLoaded(true);
    //   });
  }

  useEffect(() => {
    getAPIsData();
  }, []);

  if (!isLoaded) {
    return <h2>Loading</h2>;
  }

  
  return (
    <Router basename="/cryptojunkies">
      <main>
          <Header/>
          {/* <GlobalData globalData={globalData} /> */}
          <Switch>
            <Route 
              path="/" 
              render={(props) => (
                <Index 
                  {...props}
                  news={news}
                  chart={chart}
                  btcFaG={fearAndGreed} 
                  trending={trending}
                />
              )}  
              exact 
            />
          </Switch>
      </main>
  </Router>
  );
}

export default App;
